.hero{margin-bottom:45px;position:relative;z-index:1;width:100%;background-color:#fff1e7}@media(min-width: 768px){.hero{margin-bottom:60px}}.hero__text{position:relative;z-index:1;display:flex;flex-wrap:wrap;align-content:center;align-items:center;width:100%;max-width:380px;font-size:.875rem;line-height:1.4;padding-top:61px;padding-bottom:20px}@media(min-width: 768px){.hero__text{min-height:400px;font-size:1rem;line-height:1.5;padding-top:20px;padding-bottom:20px}}@media(min-width: 992px){.hero__text{max-width:480px;min-height:500px}}.hero h1{margin-bottom:16px;font-size:32px;line-height:1.4;letter-spacing:-0.5px}.hero p{margin-bottom:34px}@media(min-width: 768px){.hero p{margin-bottom:43px}}.hero__bg{position:relative;width:100%}@media(min-width: 768px){.hero__bg{position:absolute;top:0;right:0;z-index:-1;width:60%;height:100%}}.hero__bg:before{position:absolute;top:0;left:0;width:100%;height:100%;background-image:linear-gradient(to bottom, rgb(255, 241, 231) 0%, rgba(255, 241, 231, 0) 100%);content:""}@media(min-width: 768px){.hero__bg:before{display:block;width:60%;background-image:linear-gradient(90deg, rgb(255, 241, 231) 30%, rgba(255, 241, 231, 0) 65.05%)}}@media(min-width: 992px){.hero__bg:before{width:40%;background-image:linear-gradient(90deg, rgb(255, 241, 231) 25%, rgba(255, 241, 231, 0) 65.05%)}}.hero__bg img{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;-o-object-position:center center;object-position:center center}